"use client";

import { useState, useEffect } from "react";
import {
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  Collapse,
  Box,
  Typography,
  Chip,
  Divider,
  useTheme,
  Paper,
  CircularProgress,
  ListItemIcon,
} from "@mui/material";
import { alpha } from "@mui/material/styles";
import { ExpandLess, ExpandMore, CheckCircle, Code } from "@mui/icons-material";

export default function DSAChapterList({
  chapters,
  onSelectChapter,
  selectedChapter,
  progress,
}) {
  const [expanded, setExpanded] = useState({});

  const handleExpand = (chapterId) => {
    setExpanded((prev) => ({
      ...prev,
      [chapterId]: !prev[chapterId],
    }));
  };

  const getCompletionStatus = (chapter) => {
    const totalSubtopics = chapter.subtopics?.length || 0;
    if (totalSubtopics === 0) return 0;

    const completedSubtopics = chapter.subtopics.filter(
      (subtopic) => progress[chapter.id]?.[subtopic.id]
    ).length;

    return Math.round((completedSubtopics / totalSubtopics) * 100);
  };

  if (chapters.length === 0) {
    return (
      <Paper
        elevation={0}
        sx={{
          p: 3,
          textAlign: "center",
          backgroundColor: theme.palette.background.paper,
          height: "100%",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          minHeight: 200,
        }}
      >
        <Box sx={{ mb: 2 }}>
          <Code sx={{ fontSize: 40, color: "text.secondary" }} />
        </Box>
        <Typography variant="body1" color="text.secondary">
          No chapters available
        </Typography>
        <Typography variant="body2" color="text.disabled" sx={{ mt: 1 }}>
          Check back later for updates
        </Typography>
      </Paper>
    );
  }

  return (
    <Paper
      elevation={0}
      sx={{
        height: "100%",
        overflow: "auto",
        backgroundColor: theme.palette.background.paper,
        border: `1px solid ${theme.palette.divider}`,
        borderRadius: 2,
      }}
    >
      <List component="nav" sx={{ width: "100%", bgcolor: "background.paper" }}>
        {chapters.map((chapter) => (
          <Box key={chapter._id}>
            <ListItemButton
              onClick={() => {
                handleClick(chapter._id);
                handleChapterSelect(chapter);
              }}
              selected={selectedChapter?._id === chapter._id}
              sx={{
                borderRadius: 1,
                "&.Mui-selected": {
                  backgroundColor: "primary.light",
                  "&:hover": {
                    backgroundColor: "primary.light",
                  },
                },
              }}
            >
              <ListItemText
                primary={
                  <Typography variant="subtitle1" fontWeight={500}>
                    {chapter.name}
                  </Typography>
                }
                secondary={`${chapter.subtopics?.length || 0} problems`}
              />
              {openChapters[chapter._id] ? <ExpandLess /> : <ExpandMore />}
            </ListItemButton>

            <Collapse
              in={openChapters[chapter._id]}
              timeout="auto"
              unmountOnExit
            >
              <List component="div" disablePadding>
                {chapter.subtopics?.map((subtopic) => (
                  <ListItem
                    key={subtopic._id}
                    sx={{
                      pl: 4,
                      py: 0.5,
                      position: "relative",
                      "&:hover": {
                        backgroundColor: alpha(
                          theme.palette.primary.main,
                          0.04
                        ),
                      },
                    }}
                    secondaryAction={
                      subtopic.isCompleted && (
                        <CheckCircle color="success" fontSize="small" />
                      )
                    }
                  >
                    <ListItemButton
                      onClick={() => handleChapterSelect(chapter, subtopic)}
                      sx={{
                        pl: 2,
                        borderRadius: 1,
                        "&.Mui-selected": {
                          backgroundColor: alpha(
                            theme.palette.primary.main,
                            0.08
                          ),
                          "&:hover": {
                            backgroundColor: alpha(
                              theme.palette.primary.main,
                              0.12
                            ),
                          },
                        },
                      }}
                      selected={selectedChapter?._id === chapter._id}
                    >
                      <ListItemText
                        primary={
                          <Box
                            sx={{
                              display: "flex",
                              alignItems: "center",
                              gap: 1,
                            }}
                          >
                            <Typography variant="body2">
                              {subtopic.name}
                            </Typography>
                            <Chip
                              label={subtopic.difficulty}
                              size="small"
                              color={getLevelColor(subtopic.difficulty)}
                              variant="outlined"
                            />
                          </Box>
                        }
                        primaryTypographyProps={{ variant: "body2" }}
                      />
                    </ListItemButton>
                  </ListItem>
                ))}
              </List>
            </Collapse>
            <Divider sx={{ my: 1 }} />
          </Box>
        ))}
      </List>
    </Paper>
  );
}
